body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  max-width: 600px;
  margin: auto;
  margin-top: 2em;
  padding: 1em;
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px 0px rgba(0, 0, 0, 0.1);
}

h1 {
  font-weight: normal;
  margin: 0;
}

.tasks {
  list-style: none;
  padding: 0;
}

.done {
  text-decoration: line-through;
  opacity: 0.5;
}

input[type="text"],
input[type="submit"] {
  font-size: 0.9em;
  padding: 4px 8px;
}

label {
  vertical-align: top;
}

.delete-button {
  padding-top: 4px;
  margin-left: 8px;
  cursor: pointer;
  opacity: 0.3;
  visibility: hidden;
}

li:hover .delete-button {
  visibility: visible;
}

.delete-button:hover {
  opacity: 0.6;
}

